import React, { useState, useRef } from "react";
import PhoneInput from "react-phone-input-2";

import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";

import {
  faIdCard,
  faKey,
  faEye,
  faEyeSlash,
  faBirthdayCake,
  faHashtag,
  faMoneyBill,
  faPercent,
  faDollarSign,
  faCreditCard,
  faCity,
  faFlag,
} from "@fortawesome/free-solid-svg-icons";

import "react-phone-input-2/lib/material.css";
import specialities from "../data/specialities";

import {
  Button,
  FormControl,
  FormLabel,
  RadioGroup,
  FormControlLabel,
  Radio,
  Grid,
  Checkbox,
} from "@material-ui/core";
import classes from "./RegistrationForm.module.css";
import Input from "./Input";
import Center from "./Center";
import {
  AccountCircle,
  EmailRounded,
  LocalHospitalRounded,
} from "@mui/icons-material";
import { Divider, IconButton, Typography } from "@mui/material";

import { blue, grey } from "@mui/material/colors";

var specialitiesEnglish = [];
var specialitiesArabic = [];
var selectedLanguages = [];

const allLanguages = [
  { language: "English", isSelected: false },
  { language: "Arabic", isSelected: false },
];

const education = {
  institutionName: "",
  course: "",
  year: "",
};

const experience = {
  hospitalName: "",
  position: "",
  years: "",
};

const iconColor = "#42a5f5";
const iconErrorColor = "#f44336";

function RegistrationForm() {
  const [gender, setGender] = useState("MALE");
  const [gradeLevel, setGradeLevel] = useState("CONSULTANT");
  const [phoneNumber, setPhoneNumber] = useState("");
  const [passwordState, setPasswordState] = useState("password");
  const [confirmPasswordState, setConfirmPasswordState] = useState("password");
  const [invalidDiscountPercentage, setInvalidDiscountPercentage] =
    useState(false);

  const [passwords, setPasswords] = useState({
    password: "",
    confirmPassword: "",
    passwordsMatch: true,
  });
  const [discountPercentageDisabled, setDiscountPercentageDisabled] =
    useState(true);
  const [selectedSpecialities, setSelectedSpecialities] = useState(
    specialities.map((spaciality) => {
      return {
        en: spaciality.en,
        ar: spaciality.ar,
        icon: spaciality.icon,
        isSelected: spaciality.isSelected,
      };
    })
  );

  const [languages, setLanguages] = useState([
    { language: "English", isSelected: false },
    { language: "Arabic", isSelected: false },
  ]);

  const discountAmountRef = useRef();
  const discountPercentageRef = useRef();
  const feeRef = useRef();

  const formSubmitHandler = (event) => {
    event.preventDefault();
  };
  const togglePasswordObscuredHandler = (event) => {
    if (passwordState === "password") {
      setPasswordState("text");
    } else {
      setPasswordState("password");
    }
  };
  const toggleConfirmPasswordObscuredHandler = (event) => {
    if (confirmPasswordState === "password") {
      setConfirmPasswordState("text");
    } else {
      setConfirmPasswordState("password");
    }
  };

  const onDiscountPercentageChange = (event) => {
    if (+event.target.value > 99 || +event.target.value < 0) {
      setInvalidDiscountPercentage(true);
      discountPercentageRef.current.setValue("0");
    } else {
      if (invalidDiscountPercentage) {
        setInvalidDiscountPercentage(false);
      }
    }

    updateDiscountAmount();
  };

  const updateDiscountAmount = () => {
    const discountPercentage = discountPercentageRef.current.getValue();

    const discountAmount =
      feeRef.current.getValue() * (discountPercentage / 100);
    discountAmountRef.current.setValue(discountAmount);
  };

  const onFeeChange = (event) => {
    if (event.target.value === "") {
      if (!discountPercentageDisabled) {
        setDiscountPercentageDisabled(true);
        discountPercentageRef.current.setValue("");
      }
    } else {
      if (discountPercentageDisabled) {
        setDiscountPercentageDisabled(false);
      }
    }

    updateDiscountAmount();
  };

  const validatePassword = (pass, confirmPass) => {
    console.log(pass === confirmPass);
    return pass === confirmPass;
  };

  return (
    <React.Fragment>
      <Center>
        <form onSubmit={formSubmitHandler} className={classes.form}>
          <div className={classes.row}>
            <Input
              required
              fullWidth
              type="text"
              placeholder="Enter your first name"
              label="First Name"
              icon={<AccountCircle sx={{ color: blue[400] }} />}
            ></Input>
            <Input
              required
              type="text"
              label="Last Name"
              placeholder="Enter your last name"
              icon={<AccountCircle sx={{ color: blue[400] }} />}
            ></Input>
          </div>
          <FormControl>
            <FormLabel>Gender</FormLabel>
            <RadioGroup
              row
              value={gender}
              onChange={(event) => {
                setGender(event.target.value);
              }}
              name="gender"
            >
              <FormControlLabel
                label="Male"
                value="MALE"
                control={<Radio color="primary" />}
              />
              <FormControlLabel
                label="Female"
                value="FEMALE"
                control={<Radio color="primary" />}
              />
            </RadioGroup>
          </FormControl>

          <div
            style={{
              display: "flex",
              justifyContent: "center",
              alignItems: "center",
            }}
          >
            <PhoneInput
              country={"sa"}
              inputProps={{
                required: true,
              }}
              placeholder="Enter phone number"
              value={phoneNumber}
              onChange={setPhoneNumber}
              onlyCountries={["ae", "pk", "sa", "kw", "bh", "gb", "us"]}
              countryCodeEditable={false}
              enableSearch={true}
              inputStyle={{
                width: "100%",
                height: "55px",
              }}
            />
          </div>

          <Input
            type="email"
            label="Email"
            placeholder="Enter your email"
            icon={<EmailRounded sx={{ color: blue[400] }} />}
          ></Input>
          <Input
            type="text"
            label="Hospital Name"
            placeholder="Enter the name of your hospital"
            icon={<LocalHospitalRounded sx={{ color: blue[400] }} />}
          ></Input>

          <Input
            type="number"
            required
            label="National ID/Iqama"
            placeholder="Enter your national ID number"
            icon={
              <FontAwesomeIcon
                style={{
                  width: "25px",
                  height: "25px",
                }}
                icon={faIdCard}
                color={iconColor}
              />
            }
          ></Input>

          <Input
            type={passwordState}
            required
            onChange={(event) => {
              setPasswords((prevState) => {
                return {
                  ...prevState,
                  password: event.target.value,
                  passwordsMatch: validatePassword(
                    event.target.value,
                    prevState.confirmPassword
                  ),
                };
              });
            }}
            label="Password"
            placeholder="Enter your password"
            icon={
              <FontAwesomeIcon
                style={{
                  width: "25px",
                  height: "25px",
                }}
                icon={faKey}
                color={iconColor}
              />
            }
            suffix={
              <IconButton onClick={togglePasswordObscuredHandler}>
                <FontAwesomeIcon
                  style={{
                    width: "25px",
                    height: "25px",
                  }}
                  icon={passwordState === "password" ? faEye : faEyeSlash}
                  color={iconColor}
                  aria-label="toggle password visibility"
                  label="Password"
                />
              </IconButton>
            }
          />
          <Input
            onChange={(event) => {
              setPasswords((prevState) => {
                return {
                  ...prevState,
                  confirmPassword: event.target.value,
                  passwordsMatch: validatePassword(
                    prevState.password,
                    event.target.value
                  ),
                };
              });
            }}
            error={!passwords.passwordsMatch}
            helperText={
              !passwords.passwordsMatch ? "Passwords don't match" : ""
            }
            type={confirmPasswordState}
            required
            label="Confirm Password"
            placeholder="Confirm your password"
            icon={
              <FontAwesomeIcon
                style={{
                  width: "25px",
                  height: "25px",
                }}
                icon={faKey}
                color={iconColor}
              />
            }
            suffix={
              <IconButton onClick={toggleConfirmPasswordObscuredHandler}>
                <FontAwesomeIcon
                  style={{
                    width: "25px",
                    height: "25px",
                  }}
                  icon={
                    confirmPasswordState === "password" ? faEye : faEyeSlash
                  }
                  color={iconColor}
                />
              </IconButton>
            }
          />

          <Input
            type="date"
            required
            label="Date of birth"
            onChange={(event) => {
              console.log(event.target.value);
            }}
            icon={
              <FontAwesomeIcon
                style={{
                  width: "25px",
                  height: "25px",
                }}
                icon={faBirthdayCake}
                color={iconColor}
              />
            }
          />

          <Input
            type="text"
            required
            label="SCFHS Registration Number"
            placeholder="Enter SCFHS Number"
            onChange={(event) => {
              console.log(event.target.value);
            }}
            icon={
              <FontAwesomeIcon
                style={{
                  width: "25px",
                  height: "25px",
                }}
                icon={faHashtag}
                color={iconColor}
              />
            }
          />

          <FormControl>
            <FormLabel>Grade Level</FormLabel>
            <RadioGroup
              row
              value={gradeLevel}
              onChange={(event) => {
                setGradeLevel(event.target.value);
              }}
              name="gradeLevel"
            >
              <FormControlLabel
                label="Consultant"
                value="CONSULTANT"
                control={<Radio color="primary" />}
              />
              <FormControlLabel
                label="Specialist"
                value="SPECIALIST"
                control={<Radio color="primary" />}
              />
            </RadioGroup>
          </FormControl>
          <Divider textAlign="left">
            <Typography fontSize={16} color={grey[700]}>
              Select your specialities
            </Typography>
          </Divider>

          <Grid container spacing={2}>
            {specialities.map((speciality, index) => {
              return (
                <Grid key={speciality.en} item xs={6}>
                  <FormControlLabel
                    label={speciality.en}
                    control={
                      <Checkbox
                        color="primary"
                        checked={selectedSpecialities[index].isSelected}
                        onChange={(event) => {
                          selectedSpecialities[index].isSelected =
                            event.target.checked;
                          setSelectedSpecialities((prevSpecialities) => {
                            return {
                              ...prevSpecialities,
                            };
                          });
                          if (event.target.checked) {
                            specialitiesEnglish.push(
                              selectedSpecialities[index].en
                            );
                            specialitiesArabic.push(
                              selectedSpecialities[index].ar
                            );
                          } else {
                            specialitiesEnglish = specialitiesEnglish.filter(
                              (specialityEnglish) =>
                                specialityEnglish !==
                                selectedSpecialities[index].en
                            );
                            specialitiesArabic = specialitiesArabic.filter(
                              (specialityArabic) =>
                                specialityArabic !==
                                selectedSpecialities[index].ar
                            );
                          }
                        }}
                      />
                    }
                  ></FormControlLabel>
                </Grid>
              );
            })}
          </Grid>

          <Divider textAlign="left">
            {" "}
            <Typography fontSize={16} color={grey[700]}>
              Fee
            </Typography>
          </Divider>

          <Input
            required
            ref={feeRef}
            onChange={onFeeChange}
            fullWidth
            type="number"
            placeholder="Enter your fee in Saudi Riyals"
            label="Fee"
            icon={
              <FontAwesomeIcon
                style={{
                  width: "25px",
                  height: "25px",
                }}
                icon={faMoneyBill}
                color={iconColor}
              />
            }
          ></Input>

          <div className={classes.row}>
            <Input
              fullWidth
              ref={discountPercentageRef}
              onChange={onDiscountPercentageChange}
              error={invalidDiscountPercentage}
              disabled={discountPercentageDisabled}
              helperText={
                invalidDiscountPercentage
                  ? "Percentage must be from 0 to 99"
                  : ""
              }
              type="number"
              inputProps={{ max: 99 }}
              placeholder="Discount %"
              label="Discount"
              icon={
                <FontAwesomeIcon
                  style={{
                    width: "25px",
                    height: "25px",
                  }}
                  icon={faPercent}
                  color={
                    !invalidDiscountPercentage ? iconColor : iconErrorColor
                  }
                />
              }
            ></Input>
            <Input
              fullWidth
              type="number"
              placeholder="Discount amount "
              disabled
              ref={discountAmountRef}
            ></Input>
          </div>
          <Divider textAlign="left">
            {" "}
            <Typography fontSize={16} color={grey[700]}>
              Select your languages
            </Typography>
          </Divider>

          <Grid container spacing={2}>
            {allLanguages.map((language, index) => {
              return (
                <Grid key={language.language} item xs={6}>
                  <FormControlLabel
                    label={language.language}
                    control={
                      <Checkbox
                        color="primary"
                        checked={languages[index].isSelected}
                        onChange={(event) => {
                          languages[index].isSelected = event.target.checked;
                          setLanguages((prevLanguages) => {
                            return {
                              ...prevLanguages,
                            };
                          });
                          if (event.target.checked) {
                            selectedLanguages.push(languages[index].language);
                          } else {
                            selectedLanguages = selectedLanguages.filter(
                              (element) => {
                                return element !== allLanguages[index].language;
                              }
                            );
                          }

                          console.log(selectedLanguages);
                        }}
                      />
                    }
                  ></FormControlLabel>
                </Grid>
              );
            })}
          </Grid>
          <Divider textAlign="left">
            {" "}
            <Typography fontSize={16} color={grey[700]}>
              Education Details
            </Typography>
          </Divider>
          <div className={classes.educationContainer}>
            <Input
              fullWidth
              placeholder="Institution Name"
              variant="standard"
              onChange={(event) => {
                education.institutionName = event.target.value;
              }}
            />
            <Input
              fullWidth
              placeholder="Course"
              variant="standard"
              onChange={(event) => {
                education.course = event.target.value;
              }}
            />
            <Input
              fullWidth
              type="number"
              placeholder="Year"
              variant="standard"
              onChange={(event) => {
                education.year = event.target.value;
              }}
            />
          </div>

          <Divider textAlign="left">
            {" "}
            <Typography fontSize={16} color={grey[700]}>
              Experience Details
            </Typography>
          </Divider>
          <div className={classes.educationContainer}>
            <Input
              fullWidth
              placeholder="Hospital Name"
              variant="standard"
              onChange={(event) => {
                experience.hospitalName = event.target.value;
              }}
            />
            <Input
              fullWidth
              placeholder="Position"
              variant="standard"
              onChange={(event) => {
                experience.position = event.target.value;
              }}
            />
            <Input
              fullWidth
              type="number"
              placeholder="Years"
              variant="standard"
              onChange={(event) => {
                experience.years = event.target.value;
              }}
            />
          </div>
          <Divider textAlign="left">
            {" "}
            <Typography fontSize={16} color={grey[700]}>
              Payment Details
            </Typography>
          </Divider>

          <Input
            fullWidth
            placeholder="Enter your bank name"
            label="Bank Name"
            required
            icon={
              <FontAwesomeIcon
                style={{
                  width: "25px",
                  height: "25px",
                }}
                icon={faDollarSign}
                color={iconColor}
              />
            }
          ></Input>
          <Input
            fullWidth
            placeholder="Enter your IBAN"
            label="IBAN"
            required
            icon={
              <FontAwesomeIcon
                style={{
                  width: "25px",
                  height: "25px",
                }}
                icon={faCreditCard}
                color={iconColor}
              />
            }
          ></Input>
          <Divider textAlign="left"></Divider>
          <div className={classes.row}>
            <Input
              fullWidth
              className={classes.country}
              label="Country"
              disabled
              required
              icon={
                <FontAwesomeIcon
                  style={{
                    width: "25px",
                    height: "25px",
                  }}
                  icon={faFlag}
                  color={iconColor}
                />
              }
            ></Input>{" "}
            <Input
              fullWidth
              className={classes.city}
              placeholder="Enter your city"
              label="City"
              required
              icon={
                <FontAwesomeIcon
                  style={{
                    width: "25px",
                    height: "25px",
                  }}
                  icon={faCity}
                  color={iconColor}
                />
              }
            ></Input>
          </div>

          <div className={classes.button}>
            <Button variant="contained" color="primary" type="submit">
              Submit
            </Button>
          </div>
        </form>
      </Center>
    </React.Fragment>
  );
}

export default RegistrationForm;
